body {
  font-family: 'Roboto', sans-serif;
}
h1 {
  font-size: 48px;
  margin: 0;
  margin-bottom: 35px;
  line-height: 1.1;
  margin-top: 20px;
  margin-bottom: 20px;
}
@media screen and (max-height: 900px) {
  h1 {
    margin-bottom: 20px;
    margin-top: 10px;
  }
}
@media screen and (max-width: 767px) {
  h1 {
    font-size: 30px;
    margin-top: 30px;
  }
}
h2,
h3 {
  color: #1badc3;
  font-weight: 700;
}
h2 {
  font-size: 30px;
  margin-top: 0;
}
@media screen and (max-width: 767px) {
  h2 {
    font-size: 24px;
    padding: 0 15px;
  }
}
h3 {
  font-size: 24px;
}
a {
  color: #1badc3;
}
a:hover {
  color: #1badc3;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
ul li {
  display: inline-block;
}
@media screen and (max-width: 767px) {
  br {
    display: none;
  }
}
main {
  text-align: center;
}
header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  z-index: 1000;
}
input[type="text"] {
  padding: 22px 40px;
  border-radius: 0;
  border: 0;
  background-color: #fff;
  box-shadow: 0px 3px 20.16px 0.84px rgba(33, 33, 33, 0.22);
  line-height: 1;
  text-align: center;
}
input[type="text"]:focus {
  outline: none;
}
input[type="text"].invalid {
  box-shadow: 0px 3px 20.16px 0.84px rgba(255, 0, 0, 0.5);
}
footer {
  padding: 60px 40px;
  text-align: center;
}
@media screen and (max-height: 900px) {
  footer {
    padding: 20px 40px;
  }
}
@media screen and (max-width: 767px) {
  footer {
    padding: 40px 0;
    text-align: center;
  }
}
footer > div {
  display: inline-block;
  width: 33.33333333%;
  vertical-align: top;
}
@media screen and (max-width: 767px) {
  footer > div {
    width: 100%;
  }
}
footer .main-site {
  float: left;
  text-align: left;
}
@media screen and (max-width: 767px) {
  footer .main-site {
    float: none;
    margin: auto;
    width: 220px;
    margin-bottom: 20px;
  }
}
footer .sound {
  width: 30px;
  height: 30px;
  overflow: hidden;
  margin: auto;
  margin-top: -5px;
  cursor: pointer;
  margin-bottom: 10px;
  wrap: nowrap;
}
@media screen and (max-width: 767px) {
  footer .sound {
    display: none;
  }
}
footer .sound.active > div {
  top: -29px;
}
footer .sound > div {
  display: inline-block;
  vertical-align: top;
  position: relative;
}
footer .socials {
  text-align: center;
}
footer .socials li {
  margin: 0 8px;
  transform-origin: center;
}
footer .socials li:hover {
  transform: scale(1.2);
}
@media screen and (max-width: 767px) {
  footer .socials li {
    transform: scale(1.4);
    transform-origin: center;
  }
}
@media screen and (max-width: 767px) {
  footer .socials {
    margin-bottom: 20px;
  }
}
@media screen and (max-width: 767px) {
  footer .hashtag {
    margin-top: 20px;
  }
}
footer .copyright {
  float: right;
  text-align: right;
}
@media screen and (max-width: 767px) {
  footer .copyright {
    float: none;
    margin: auto;
    text-align: center;
  }
}
footer .copyright > div {
  float: right;
  width: 240px;
  text-align: left;
}
@media screen and (max-height: 900px) {
  footer .copyright > div {
    text-align: center;
    width: 100%;
  }
}
audio {
  display: none;
}
section {
  height: calc(100vh - 170px);
  width: 100%;
  position: relative;
}
@media screen and (max-height: 900px) {
  section {
    height: calc(100vh - 90px);
  }
}
@media screen and (max-height: 800px) {
  section {
    height: auto;
  }
}
section .content {
  display: table-cell;
  padding-bottom: 30px;
}
@media screen and (max-width: 767px) {
  section .content {
    display: block;
  }
}
section #content-slider {
  height: 100%;
  z-index: 3;
  overflow: hidden;
}
.send-page section #content-slider {
  overflow: auto;
}
section #content-slider .owl-stage-outer,
section #content-slider .owl-stage {
  height: 100%;
}
section #content-slider .owl-item {
  display: table;
  height: 100%;
}
.magenta {
