.steps-bar {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 100%;
  position: relative;
}
.steps-bar:after {
  content: '';
  clear: both;
  display: block;
}
.steps-bar .circle {
  width: 30px;
  height: 30px;
  color: #1badc3;
  font-weight: 700;
  display: inline-block;
  border-radius: 50%;
  border: 2px dotted #1badc3;
  text-align: center;
  vertical-align: middle;
  line-height: 30px;
  position: relative;
  float: right;
  transition: all 500ms;
  transition-delay: 0ms;
  font-size: 16px;
  box-sizing: content-box;
}
.steps-bar .step {
  width: calc(100% / 3 - 30px /  3);
  float: left;
  text-align: left;
  position: relative;
}
.steps-bar .step:first-child {
  width: 30px;
}
.steps-bar .step:before,
.steps-bar .step:after {
  content: '';
  display: inline-block;
  position: absolute;
  border-bottom: 2px dotted #1badc3;
  width: calc(100% - 30px - 2px);
  margin-top: 16px;
  height: 0;
  transition: all 500ms;
}
.steps-bar .step:after {
  border-bottom: 2px solid #1badc3;
  width: 0;
  transition-delay: 500ms;
}
.steps-bar .step:fisrt-child:after,
.steps-bar .step:first-child:before {
  border: 0;
}
.steps-bar .step.active .circle {
  background-color: #1badc3;
  color: white;
  transition-delay: 500ms;
}
.steps-bar .step.active:after {
  width: calc(100% - 30px - 1px);
  transition-delay: 0ms;
}
